form {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 2px solid rgba(0, 4, 255, .51);
  border-radius: 4px;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 5px;
  padding: 20px 30px;
  display: flex;
}

.btn {
  color: #f0f8ff;
  background: linear-gradient(25deg, #060e83 0%, #0c19b4 100%);
  border: none;
}

.btn:hover {
  background: linear-gradient(0deg, #0003ff 0%, #027efb 100%);
}

input {
  width: 100%;
  height: 40px;
  border-color: #060e83;
  border-radius: 5px;
  margin: 20px;
  padding: 10px;
  box-shadow: -5px -5px 20px rgba(255, 255, 255, .9), -4px -4px 5px rgba(255, 255, 255, .9), 5px 5px 20px rgba(0, 0, 0, .2), 4px 4px 5px rgba(0, 0, 0, .3);
}

